import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 0,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 0,
    todoText: "Clean my room",
    todoComplete: false
  }
];

var listElement = document.querySelector("ul.todoList");

function addTask(item) {
  let itemElement = document.createElement("li");
  itemElement.textContent = item.todoText;

  if (!item.todoComplete) {
    let editButton = document.createElement("span");
    editButton.setAttribute("class", "editBtn");
    let editIcon = document.createElement("i");
    editIcon.setAttribute("class", "fa fa-edit");
    editButton.appendChild(editIcon);
    itemElement.appendChild(editButton);
  } else {
    itemElement.setAttribute("class", "done");
  }

  let trashButton = document.createElement("span");
  let trashIcon = document.createElement("i");
  trashIcon.setAttribute("class", "fa fa-trash");
  trashButton.appendChild(trashIcon);
  itemElement.appendChild(trashButton);

  listElement.appendChild(itemElement);

  itemElement.addEventListener("click", () => {
    if (itemElement.getAttribute("class") === "done") {
      itemElement.setAttribute("class", "");
    } else {
      itemElement.setAttribute("class", "done");
    }

    // Figure out how to set the array item ID without an item ID in the DOM
  });
}

// Add todos
for (let item of todos) {
  addTask(item);
}

function handleAddButton() {
  let taskText = document.querySelector("input").value;
  let id = todos.length; // todos.length - 1 is the previous id
  let item = { todoID: id, todoText: taskText, todoComplete: false };
  todos.push(item);
  addTask(item);
  console.log(todos);
}

// Add new task
document.querySelector(".inputField button").addEventListener("click", () => {
  handleAddButton();
});

// https://developer.mozilla.org/en-US/docs/Web/API/Element/keydown_event
document.addEventListener("keydown", (event) => {
  if (event.keyCode === 13) {
    // Enter in ASCII
    handleAddButton();
  }
});
